.hero {

  background-image: url(../../img/bg-hero.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 100vh;
  padding-top: 140px;

  &__title {
    font-size: 70px;
    color: var(--text-color);
    text-align: center;
  }

  &__descr {
    color: var(--text-color-2);
    font-size: 22px;
    text-align: center;
    margin: 10px auto;
  }

  &__write {
    justify-content: center;
    display: flex;
    align-items: center;
    gap: 7px;
    padding: 8px;
    background-image:
      linear-gradient(to right,
        rgba(27, 51, 52, 0.3) 0%,
        rgba(27, 51, 52, 0.3) 100%),
      linear-gradient(to right,
        rgba(255, 255, 255, 0.16) 0%,
        rgba(255, 255, 255, 0.16) 100%);
    border: 2px solid rgba(255, 255, 255, 0.21);
    background-blend-mode: normal;
    width: max-content;
    margin: 0 auto;
    border-radius: 66px;
    box-shadow: 0 1.33px 2.67px 0 rgba(29, 255, 255, 0.55);
    box-shadow: 0 0 0 4px rgba(28, 255, 255, 0.17);


    & span {
      padding: 4px 13px;
      font-size: 16px;
      color: #D3FFFF;
      background-color: #196264;
      background-image: linear-gradient(to bottom, #207278, #237b81, #26838a, #298c94, #2c959d);
      border-radius: 66px;
      border: 1.33px solid #4D8F93;
    }

    & p {
      color: var(--text-white-0);
      font-size: 16px;
    }
  }

  &__subdescr {
    color: #fff;
    text-align: center;
    font-size: 18px;
    margin-top: 65px;
    margin-bottom: 25px;
  }

  &__get {
    margin: 0 auto;
    margin-top: 25px;
  }

  &__social {
    display: flex;
    align-items: center;
    gap: 15px;
    margin: 0 auto;
    justify-content: center;
  }

  &__social-item {
        max-width: 170px;
    border-radius: 40px;
    background-color: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(16px);
    -webkit-backdrop-filter: blur(16px);
    padding: 10px 15px;
    display: flex;
    align-items: center;
    gap: 7px;

    & span {
      font-size: 15px;
      color: #fff;
    }
  }


}
