.footer {
  background-image: url(../../img/bg-footer.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding: 80px 0 20px 0;

  &__wrap {
    display: flex;
    align-items: flex-start;
    gap: 60px;
    justify-content: space-between;
  }

  &__left{
    width: 35%;
  }

  &__logo {
    margin-bottom: 25px;

    & img {
      width: 275px;
      height: auto;
      display: block;
      object-fit: contain;
    }
  }

  &__descr {
    color: #E6E6E6;
    font-size: 16px;
    margin-bottom: 32px;
  }

  &__social {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 25px;
  }

  &__verification {
    margin-top: 10px;
    width: max-content;
    display: block;
    color: #0BCACA;
    border-bottom: 1px dotted #0BCACA;
    font-size: 18px;
  }

  &__title {
    font-size: 20px;
    color: #fff;
    margin-bottom: 30px;
    text-transform: uppercase;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 7px;
  }

  &__link {
    display: block;
    font-size: 16px;
    color: #CECFD2;
  }

  &__btn {
    margin-left: auto;
    font-size: 14px;

    & img {
      width: 21px;
      height: 21px;
    }
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: flex-end;
    margin-top: 30px;
  }

  &__contacts-item {
    display: flex;
    gap: 10px;
    align-items: flex-start;
  }

  &__contacts-text {
    color: #fff;
    font-size: 20px;
  }

  &__contacts-item:last-child span {
    min-width: 230px;
    text-align: right;
  }

  &__copyright{
    color: #fff;
    font-size: 18px;
    display: block;
    text-align: center;
    margin-top: 30px;
  }
}
