.header {

  &__container {
    position: absolute;
    z-index: 999;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    max-width: 1440px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    border: 1px solid #37393C;
    box-shadow: 0 1px 4px 0 rgba(38,40,46, 0.37);
    border-radius: 40px;
    background: rgba(11,202,202, 0.12);
    backdrop-filter: blur(4px); /* размытие фона */
  -webkit-backdrop-filter: blur(4px); /* поддержка Safari */
  }

  &__reg{
    font-size: 14px;
  }

  &__nav {
    display: flex;
    list-style-type: none;
    gap: 30px;
  }

  &__nav-link {
    color: var(--text-color);
    font-size: 16;
  }

  &__right {
    display: flex;
    align-items: center;
    gap: 22px;
  }

  &__btns {
    display: flex;
    align-items: center;
    gap: 22px;
  }

  &__login {
    padding: 13px 43px;
    font-size: 14px;
    color: var(--text-white-0);
    border: 2px solid rgba(255, 255, 255, 0.21);
    border-radius: 40px;
    box-shadow: 0 1.33px 2.67px 0 rgba(29, 255, 255, 0.55);
  }
}

.menu-btn{
  color: #fff;
}
