// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Open Sans", sans-serif;
  --content-width: 1440px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --text-color-2: #EAEAEA;
  --text-color: #CECFD2;
  --text-white-0: #fff;

  --offset-padding: 80px;
}
