.brands {

  padding: var(--offset-padding) 0;
  background-image: url(../../img/bg-brands.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;

  &__container{
display: flex;
align-items: center;
    gap: 50px;
  }

  &__left{
    width: 50%;
  }

  &__right{
    width: 50%;
  }

  &__title{
    font-size: 54px;
    position: relative;
    z-index: 100;
  }

  &__title b{
    color: #0EFFFF;
  }

  &__descr{
    color: #fff;
    font-size: 20px;
    margin-top: 10px;
    position: relative;
    z-index: 100;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    column-gap: 25px;
    row-gap: 45px;
  }

  &__element{
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
  }
}
