.btn-main {
  display: flex;
  align-items: center;
  gap: 5px;
  width: max-content;
  padding: 13px 43px;
  font-size: 18px;
  color: var(--text-white-0);
  background-color: #196264;
  background-image: url('../../img/bg-btn.svg'),
    linear-gradient(to bottom, #207278, #237b81, #26838a, #298c94, #2c959d);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  box-shadow: 0 1.33px 2.67px 0 rgba(29, 255, 255, 0.55);
  box-shadow: 0 0 0 4px rgba(28, 255, 255, 0.17);
  border: 2px solid rgba(255, 255, 255, 0.21);
  border-radius: 85px;
}

.subtitle{
  color: #13C8CB;
  font-size: 20px;
  text-transform: uppercase;
}

.title{
  font-size: 60px;
  color: #fff;
}
