.levels {
  padding: var(--offset-padding) 0;
  background-image: url(../../img/bg-levels.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  &__subtitle {
    text-align: center;
    margin-top: 10px;
  }

  &__title {
    text-align: center;
  }

  &__descr {
    color: #fff;
    font-size: 20px;
    text-align: center;
    margin-top: 10px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 45px;
    margin-top: 85px;
  }

  &__item {
    border-radius: 18px;
    background-color: #181E2A;
    padding: 25px;
    display: flex;
    flex-direction: column;
    border: 0.7px solid #E7EBFF;
  }

  &__icon {
    width: 38px;
    height: auto;
    display: block;
    object-fit: contain;
    margin-bottom: 14px;
  }

  &__head {
    margin-bottom: 9px;
    color: #fff;
    font-size: 28px;
    text-align: left;
  }

  &__subdescr {
    color: #fff;
    font-size: 16px;
    margin-bottom: 16px;
  }

  &__price{
    margin-bottom: 19px;
    padding-bottom: 19px;
    border-bottom: 1px solid #E7EBFF;
  }

  &__price-cost{
    font-size: 40px;
    color: #fff;
    margin-right: 7px;
  }

  &__price-month{
    font-size: 16px;
    color: #fff;
  }

  &__sublist{
    margin-bottom: 30px;
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__subitem{
    color: #fff;
    display: flex;
    align-items: flex-start;
    gap: 5px;
  }

  &__btn{
    font-size: 16px;
    color: #fff;
    display: block;
    width: 100%;
    text-align: center;
    border-radius: 8px;
    border: 0.7px solid #fff;
    padding: 15px 0;
  }

  &__vip{
    background-image: url(../../img/bg-vip.svg);
background-size: 134% 120%;
  background-repeat: no-repeat;
  background-position: center;
  }

  &__vip &__head{
    color: #1B223C;
  }

  &__vip &__subdescr{
    color: #3C3C3C;
  }

  &__vip &__subitem{
    color: #1B223C;
  }

  &__vip &__price-cost{
    color: #1B223C;
  }

  &__vip &__price-month{
    color: #3C3C3C;
  }

  &__vip &__sublist{
    color: #1B223C;
  }

  &__vip &__btn{
    background-color: #1B223C;
    color: #fff;
    border: 1px solid #152C80;
  }
}
