.about {

  background-image: url(../../img/bg-about.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding: var(--offset-padding) 0;

  &__wrap {
    display: flex;
    align-items: flex-start;
    gap: 45px;
  }

  &__left {
    width: 50%;
  }

  &__right {
    width: 50%;
  }

  &__descr {
    color: #fff;
    font-size: 18px;
    margin: 10px 0;
  }

  &__descr-2 {
    color: #DADADA;
    font-size: 18px;
    margin-bottom: 30px;
  }

  &__info {
    gap: 130px;
    display: flex;
    align-items: flex-start;
    margin-bottom: 40px;
  }

  &__info-title {
    font-size: 18px;
    color: #fff;
    margin-bottom: 12px;
    display: block;
  }

  &__info-number &__info-descr {
    display: block;
    color: #fff;
    font-size: 34px;
  }

  &__info-adress &__info-descr {
    display: flex;
    align-items: flex-start;
    gap: 10px;

    & span {
      color: #fff;
      font-size: 20px;
    }
  }

  &__certificate {
    display: flex;
    align-items: center;
    gap: 60px;
    padding: 25px 20px;
    border-radius: 20px;
    background-color: rgba(255, 255, 255, 0.05);
  }

  &__certificate-title {
    display: flex;
    gap: 12px;
    align-items: center;
  }

  &__certificate-title h3 {
    color: #fff;
    font-size: 24px;
  }

  &__certificate-descr {
    margin-top: 12px;
    color: #B1B5C3;
    font-size: 16px;
    padding-left: 33px;
  }

  &__btn {
    margin: 0 auto;
    margin-top: 60px;
  }
}

.offer{

  &__top{
    display: flex;
    align-items: center;
    gap: 35px;
    max-width: 820px;
    justify-content: center;
    margin: 0 auto;
  }

  &__title{
    font-size: 52px;

    & b{
      color: #0BCACA;
    }
  }

  &__descr{
    font-size: 18px;
    color: #fff;
    text-align: center;
    max-width: 1000px;
    margin: 20px auto 50px auto;
  }

  &__wrap{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 50px;
    align-items: flex-start;
  }

  &__subtitle{
    font-size: 40px;
    color: #fff;
    text-align: center;
  }

  &__list{
    padding: 50px 35px;
    background-color: rgba(255, 255, 255, 0.05);
    border-radius: 40px;
    color: #FFF;
    margin-top: 25px;
  }

  &__item{
    display: flex;
    align-items: flex-start;
    gap: 10px;

    &:not(:last-child){
      margin-bottom: 10px;
    }
  }

  &__text{
    font-size: 20px;
  }

  &__btn{
    margin: 0 auto;
    margin-top: 60px;
  }
}
